  background-color: var(--white);
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: fit-content;
  z-index: 1000;
  position: relative;
}

.navbar {
  background-color: var(--white);
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  position: relative;
}

.navbar-logo img {
  height: 120px;
  margin-right: 10px;
  vertical-align: middle;
}

.navbar-links {
  display: flex;
  gap: 2rem;
  transition: all 0.3s ease-in-out;
}

.navbar-logo a {
  color: var(--primary-dark-blue);
  font-size: 1.5rem;
  font-weight: bold;
  text-decoration: none;
}



.navbar-links a {
  color: var(--text-primary-light);
  text-decoration: none;
  font-size: 1.1rem;
  transition: color 0.3s ease;
  padding: 0.5rem 1rem;
  border-radius: 4px;
}

.navbar-links a:hover {
  color: var(--secondary-dark-blue);
  background-color: rgba(0, 0, 0, 0.05);
}

.navbar-toggle {
  display: none;
  background: none;
  border: none;
  cursor: pointer;
  padding: 1rem;
  z-index: 1001;
}

.navbar-toggle span {
  display: block;
  width: 25px;
  height: 2px;
  background-color: var(--primary-dark-blue);
  margin: 5px 0;
  transition: all 0.3s ease;
}

/* Hamburger animation */
.navbar-toggle.active span:nth-child(1) {
  transform: translateY(7px) rotate(45deg);
}

.navbar-toggle.active span:nth-child(2) {
  opacity: 0;
}

.navbar-toggle.active span:nth-child(3) {
  transform: translateY(-7px) rotate(-45deg);
}

@media (max-width: 768px) {
  .navbar-links {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background-color: var(--white);
    flex-direction: column;
    align-items: center;
    padding: 1rem 0;
    box-shadow: var(--shadow-standard-light);
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    transition: all 0.3s ease-in-out;
  }

  .navbar-links.active {
    max-height: 500px;
    opacity: 1;
    padding: 2rem 0;
  }

  .navbar-links a {
    margin: 0.5rem 0;
    padding: 1rem;
    width: 100%;
    text-align: center;
  }

  .navbar-toggle {
    display: block;
  }
}